@import url(https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@500&display=swap);
.BitcoinPayment{text-align:center;display:flex;flex-direction:column;align-items:center}.qr-code{height:300px !important;width:300px !important;aspect-ratio:auto 300px / 300px;object-fit:contain !important}

.Home #header{display:flex;justify-content:center;align-items:center;padding:15px;background-color:transparent;position:fixed;width:100%;z-index:2;transition:all 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s}.Home #header .logo{text-align:center;display:inline-block;color:#fff;margin:0;font-family:'Gemunu Libre', sans-serif;transition:all 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s}.Home #header.under_jumbo{background-color:#fff;box-shadow:rgba(0,0,0,0.12) 0px 2px 4px 0px}.Home #header.under_jumbo .logo{color:#ef233c}@media (max-width: 992px){.Home #header{background-color:#fff;box-shadow:rgba(0,0,0,0.12) 0px 2px 4px 0px}.Home #header .logo{color:#ef233c}}.Home #jumbo{min-height:375px;background-image:linear-gradient(256deg, #d90429, #f4697a 55%, #d90429);color:#fff;display:flex;flex-direction:column;justify-content:flex-end;padding:25px}@media (min-width: 500px){.Home #jumbo{background-image:linear-gradient(260deg, #d90429, #f4697a 55%, #d90429);min-height:300px}}@media (min-width: 768px){.Home #jumbo{background-image:linear-gradient(260deg, #d90429, #f4697a 55%, #d90429)}}@media (min-width: 1024px){.Home #jumbo{background-image:linear-gradient(262deg, #d90429, #f4697a 55%, #d90429)}}.Home #jumbo h2{font-size:2em}.Home #jumbo p{font-weight:500;font-size:1.25em}.Home #main{margin-bottom:25px}.Home #footer{display:block;background-color:#2b2d42;color:#fff;text-align:center;padding:35px 15px}.Home #footer .logo{text-align:center;display:inline-block;color:#fff;margin:0;font-family:'Gemunu Libre', sans-serif;transition:all 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s}


.Product{display:flex;flex-wrap:wrap;width:100%;justify-content:space-between;padding:25px 0;border-bottom:1px solid #ebebeb}.Product p{margin-top:0}.Product .image_block,.Product .date_time_block,.Product .details_block,.Product .buy_block{width:20%;padding:5px 15px}@media (max-width: 992px){.Product .image_block,.Product .date_time_block,.Product .details_block,.Product .buy_block{width:100%;padding:0}}.Product .image_block{padding:0}.Product .image_block img{width:200px;height:100px;object-fit:cover;object-position:top}@media (max-width: 992px){.Product .image_block img{width:100%;height:300px}}@media (max-width: 600px){.Product .image_block img{height:200px}}@media (max-width: 992px){.Product .image_block{margin-bottom:15px}}.Product .cart-item-actions{margin-top:20px}.Product .action-icons{height:25px;width:25px}.Product .action-icons:hover{cursor:pointer}.Product .quantity_block{display:flex}.Product .quantity_block .quantity{margin:5px 10px}.Product .quantity_block .action-icons{height:25px;width:25px}.Product .quantity_block .action-icons:hover{cursor:pointer}@media (max-width: 992px){.Product .quantity_block{margin-bottom:15px}}.Product .buy_block{display:flex;justify-content:flex-end;align-items:flex-start}.Product .buy_block .btn{background-color:#d90429;font-family:'Averta','Helvetica Neue','Helvetica','Arial','sans-serif';font-weight:600;text-transform:none;min-width:150px}@media (max-width: 992px){.Product .buy_block .btn{width:100%}}.Product .buy_block .btn:hover{background-color:#ef233c}.Product .buy_block .btn:active{background-color:#f4697a}@media (max-width: 992px){.Product .buy_block{justify-content:flex-start}}

.Modal{position:fixed;background:rgba(0,0,0,0.15);top:0;bottom:0;right:0;left:0;transition:all 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s;display:flex;justify-content:center;align-items:center;z-index:99}.Modal .modal_inner{width:90%;max-width:700px;position:absolute;top:10%;max-height:80%;height:auto;border-radius:3px;background-color:#fff;padding:0;box-shadow:rgba(0,0,0,0.2) 0px 11px 15px -7px,rgba(0,0,0,0.14) 0px 24px 38px 3px,rgba(0,0,0,0.12) 0px 9px 46px 8px;display:flex;flex-direction:column}.Modal .modal_inner hr{border-color:#f4697a}.Modal .modal_inner .close_wrapper{width:100%;position:sticky;top:0;background:#fff;padding:5px 5px 10px 5px;display:flex;justify-content:flex-end}.Modal .modal_inner .close_wrapper .close_icon{display:inline-block;color:#2b2d42;font-size:1.5em;text-align:right;cursor:pointer}.Modal .modal_inner .content_wrapper{padding:15px;padding-top:0;overflow-y:auto;overflow-x:hidden;height:auto}.Modal .modal_inner .content_wrapper .artist_block{margin-top:0;color:#d90429}.Modal .modal_inner .content_wrapper .quantity_block{display:flex;margin-bottom:15px}.Modal .modal_inner .content_wrapper .quantity_block .quantity{margin:5px 10px}.Modal .modal_inner .content_wrapper .quantity_block .action-icons{height:25px;width:25px}.Modal .modal_inner .content_wrapper .quantity_block .action-icons:hover{cursor:pointer}.Modal .modal_inner .content_wrapper .event_image{display:block;width:100%;max-height:300px;height:auto;object-fit:cover;object-position:top}

